}
.header__links a {
  transition: all 500ms ease-in-out;
  position: relative;
  border: 1px solid transparent;
  z-index: 1;
}
.header__links a::before, .header__links a::after {
  content: "";
  display: block;
  position: absolute;
  background-color: #FFFFFF;
  z-index: -1;
}
.header__links a.active, .header__links a:hover {
  border: 1px solid #000;
}
.header__links a.active::before, .header__links a:hover::before {
  top: -0.3em;
  bottom: -0.3em;
  left: 0.5em;
  right: 0.5em;
}
.header__links a.active::after, .header__links a:hover::after {
  left: -0.3em;
  right: -0.3em;
  top: 0.5em;
  bottom: 0.5em;
}
